<template>
  <q-card flat class="q-pa-sm rounded" :style="`width: ${width}`">
    <q-card-section>
      <q-toolbar class="bg-transparent q-pa-none">
        <CardIconBox v-if="icon" :icon="icon" />

        <div v-if="!noclose" class="absolute-top-right">
          <close-button />
        </div>
      </q-toolbar>
    </q-card-section>
    <q-card-section class="q-pt-none">
      <div
        v-if="messageTitle"
        class="text-h5 font-demi-bold letter-space-normal"
      >
        {{ messageTitle }}
      </div>
      <div
        v-if="messageDescription"
        class="text-body2 color-primary2 letter-space-normal q-pt-md"
      >
        {{ messageDescription }}
      </div>
    </q-card-section>
    <q-card-actions align="right" class="full-width q-gutter-sm q-py-md">
      <q-btn
        v-if="cancel"
        no-caps
        outline
        unelevated
        color="primary"
        class="col rounded slide-hover"
        @click="$emit('canceled')"
        v-close-popup
      >
        <span class="text-body1 font-medium">{{ cancelText }}</span>
      </q-btn>
      <q-btn
        v-if="ok"
        no-caps
        unelevated
        color="primary"
        class="col rounded slide-hover"
        @click="$emit('okay')"
        v-close-popup
      >
        <span class="text-body1 font-medium">{{ okText }}</span>
      </q-btn>
    </q-card-actions>
  </q-card>
</template>

<script>
import { defineComponent } from 'vue';
import CardIconBox from '../Helpers/CardIconBox.vue';

export default defineComponent({
  name: 'DynamicDialogConfirmation',

  components: { CardIconBox },

  emits: ['close', 'okay', 'canceled'],

  props: {
    noclose: {
      type: Boolean,
      default: false,
    },
    icon: {
      type: String,
      default: 'icon-check-circle-broken',
    },
    width: {
      type: String,
      default: '360px',
    },
    cancel: {
      type: Boolean,
      default: true,
    },
    cancelText: {
      type: String,
      default: 'Cancel',
    },
    ok: {
      type: Boolean,
      default: true,
    },
    okText: {
      type: String,
      default: 'Okay',
    },
    messageTitle: {
      type: String,
      default: '',
    },
    messageDescription: {
      type: String,
      default: '',
    },
  },

  unmounted() {
    this.$emit('close');
  },
});
</script>
